body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

svg {
  display: block;
  margin: auto;
  background-color: white;
}

.App {
  background-color: whitesmoke;
  height: 100vh;
  width: 100vw;
}

.App-title {
  font-size: 20pt;
  color: darkslategrey;
  padding: 5px;
}

.axis path,
.axis line {
  fill: none;
  stroke: black;
  stroke-width: 1px;
  shape-rendering: crispEdges;
}

.axis text {
  font-family: sans-serif;
  font-size: 12px;
}

.x-axis {
  color: #4295f4;
  font-size: 15px;
  position: relative;
  bottom: 24px;
  right: 20px;
}

.y-axis {
  color: #4295f4;
  font-size: 15px;
  position: relative;
  bottom: 170px;
  right: 260px;
}

.controls button {
  display: block;
  margin: 1rem auto;
  background-color: aquawhite;
}

.data_labels {
  fill: grey;
  font-size: 12px;
}

h1 {
  text-align: center;
}

svg {
  display: block;
  margin: auto;
  background-color: white;
}

rect {
  fill-opacity: 0.5;
}
rect.goodBar {
  fill: green;
}
rect.badBar {
  fill: red;
  /* fill: linear-gradient(to right, #ed3434 1%,#ad1818 100%) */
}

.axis path,
.axis line {
  fill: none;
  stroke: black;
  shape-rendering: crispEdges;
}

.axis text {
  font-family: sans-serif;
  font-size: 11px;
}

text {
  font-family: sans-serif;
  font-size: 12px;
}

.controls button {
  display: block;
  margin: 4em auto 4em auto;
  background-color: aquawhite;
}

.WaterfallConfig {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.WaterfallConfig > * {
  margin-bottom: 1rem;
}

.sideBar {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 1rem;
  max-width: 250px !important;
  -webkit-flex: 0 0 250px !important;
          flex: 0 0 250px !important;
}

.dashboard {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: flex-start;
          align-items: flex-start;
}

.dashboard > * {
  margin: 0.5rem;
}

.vizMenuExtra > * {
  margin-left: 1rem;
}

